@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,500,600,700,800);
/*
	Template by FreeHTML5.co
*/
* {
  margin: 0px;
  padding: 0px;
  font-family: "Montserrat", "sans-serif";
}

html {
  overflow-x: hidden;
  font-size: 1rem;
}

.download-btn-first {
  background: #f7f7f7;
}

.download-btn-first:hover {
  background: #234197;
  color: #fff !important;
}

.features-btn-first {
  font-weight: 700;
}

.features-btn-first:hover {
  background: #f7f7f7 !important;
  color: #234197 !important;
}

#cashierless-hero-wrapper {
  height: 800px;
  width: 100%;
  background: url(/static/media/banner-bg.d6689728.jpg) no-repeat center;
  border-radius: 0 0 50% 50% / 4%;
}

.main-navbar-nav .navbar-brand {
  font-size: 38px;
  letter-spacing: 4px;
  position: relative;
  top: -5px;
  font-weight: 500;
}

.main-navbar-nav {
  color: #fff !important;
}
.main-navbar-nav li {
  color: #fff;
  opacity: 0.7;
}
.main-navbar-nav .navbar-nav .nav-item a:hover {
  color: #234197 !important;
  transition: all 0.33s ease;
}
.main-navbar-nav .active {
  color: #fff !important;
  opacity: 1 !important;
}
.main-navbar-nav a {
  color: #fff !important;
}
.main-navbar-nav .nav-link {
  opacity: 1 !important;
  margin: 0 35px;
  font-size: 14.5px;
  font-weight: 500;
}

.social-icons-header i {
  margin: 15px;
  font-size: 20px;
}

.social-icons-header a:hover i {
  opacity: 0.6;
  transition: all 0.2s ease;
}

.cashierless-hero-inner {
  margin-top: 110px;
  position: relative;
}
.cashierless-hero-inner > h1 {
  color: #fff;
  font-weight: 300;
  font-size: 55px;
  letter-spacing: 2px;
}
.cashierless-hero-inner > p {
  width: 400px;
  font-size: 14px;
  color: #fff;
  margin-top: 30px;
  margin-bottom: 30px;
  line-height: 2em;
}
.cashierless-hero-inner .download-btn-first {
  font-size: 15px;
  padding: 10px 15px;
  font-weight: 700;
  color: #234197;
}
.cashierless-hero-inner .features-btn-first {
  font-size: 15px;
  padding: 8px 13px;
  font-weight: 400;
  background: #234197;
  color: #fff;
  margin-left: 10px;
}
.cashierless-hero-inner .cashierless-hero-smartphone {
  position: absolute;
  right: 0px;
  top: 120px;
  height: 650px;
}

/* ==========================================================================================================
										ADVANTAGES
   ========================================================================================================== */
.cashierless-advantages-outer {
  padding: 70px;
  height: auto;
  width: 100%;
  overflow: hidden;
}
.cashierless-advantages-outer .span-perfect {
  color: #648cff;
  text-transform: uppercase;
  font-size: 27px;
  letter-spacing: 1px;
}
.cashierless-advantages-outer .span-features {
  text-transform: uppercase;
  font-size: 42px;
}
.cashierless-advantages-outer .second-title {
  text-align: center;
  display: block;
}
.cashierless-advantages-outer small {
  display: block;
  text-align: center;
}
.cashierless-advantages-outer .cashierless-advantages-grid-columns {
  margin-top: 120px;
}
.cashierless-advantages-outer
  .cashierless-advantages-grid-columns
  .col-sm-4
  .grid-image {
  width: 30%;
  float: left;
  padding-right: 30px;
  position: relative;
  top: 1px;
  color: #648cff;
}
.cashierless-advantages-outer
  .cashierless-advantages-grid-columns
  .col-sm-4
  .grid-title {
  float: left;
  font-size: 20px;
  font-weight: 600;
}
.cashierless-advantages-outer
  .cashierless-advantages-grid-columns
  .col-sm-4
  .grid-desc {
  float: right;
  font-size: 13px;
  padding-right: 7%;
  width: 70%;
  color: #c7c7c7;
  margin-top: 10px;
  line-height: 1.87em;
}

.cashierless-slider-inner {
  margin-top: 50px;
  margin-bottom: 30px;
}

/* ==========================================================================================================
										SLIDER
   ========================================================================================================== */
.cashierless-slider-outer {
  width: 100%;
  height: auto;
  padding: 70px;
}
.cashierless-slider-outer h1 {
  color: #648cff;
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 10px;
  text-align: center;
}
.cashierless-slider-outer > h1,
.cashierless-slider-outer small {
  text-align: center;
  display: block;
}
.cashierless-slider-outer .cashierless-slider-outer {
  margin-top: 50px;
}
.cashierless-slider-outer .carousel-left-arrow {
  position: absolute;
  left: -100px;
  bottom: 250px;
  font-size: 60px;
  color: #ececec;
}
.cashierless-slider-outer .carousel-right-arrow {
  position: absolute;
  right: -100px;
  bottom: 250px;
  font-size: 60px;
  color: #ececec;
}
.cashierless-slider-outer .item img {
  height: 400px;
  width: 200px;
  object-fit: contain;
}

